@import "../variables.scss";

.event-wrapper {
  position: relative;
}
.event {
  background: $hightlight-color;
  padding: 3px 5px;
  border-radius: 5px;
  text-transform: capitalize;
  cursor: pointer;

  &.done {
    border: 2px solid rgb(74, 161, 255);
    text-decoration: line-through;
    background: unset;
  }
}
