@import "../variables.scss";
.context-menu {
  position: absolute;
  background-color: rgb(250, 250, 250);
  padding: 5px;
  border-radius: 5px;
  border: 1px solid $border-color;
  box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.6);
  top: calc(100% + 1px);
  z-index: 2;
}

.context-menu-item {
  border-radius: 5px;
  padding: 4px;
  cursor: pointer;
  &:hover {
    background-color: rgba(0, 123, 255, 0.575);
  }
}
.separator {
  height: 1px;
  background-color: $border-color;
  margin: 2px -5px;
}
