@import "../variables.scss";
.create-animal-modal {
  position: absolute;
  top: calc(100% + 1px);
  z-index: 2;
  top: -10px;
  left: calc(100% + 5px);
  background: rgb(250, 250, 250);
  padding: 10px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  gap: 5px;
  box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.6);
  border: 1px solid $border-color;
}
