.auth-page {
  display: flex;
  flex-direction: column;
  background-color: white;
  padding: 15px;
  border-radius: 10px;
  box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.6);
  position: relative;
}
.auth-page__buttons {
  margin-top: 5px;
  display: flex;
  justify-content: space-between;
}
.auth-page-error {
  background-color: white;
  padding: 15px;
  border-radius: 10px;
  box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.6);
  color: red;
  position: absolute;
  width: max-content;
  top: calc(100% + 5px);
  left: 50%;
  transform: translateX(-50%);
}
