@import "../variables.scss";
.animals {
  grid-area: animals;
  display: flex;
  align-items: center;
  padding: 0 5px;
  gap: 10px;
}
.animals-list {
  display: flex;
  gap: 10px;
}
.animal-light {
  cursor: pointer;
  position: relative;
  color: $hightlight-color;
  &.selected::after,
  &:hover::after {
    background-color: $hightlight-color;
    position: absolute;
    content: "";
    width: 100%;
    height: 1px;
    bottom: -1px;
    left: 0;
  }
}
.animal-wrapper {
  position: relative;
}
.create-animal-wrapper {
  position: relative;
}
.create-animal-button {
  width: 20px;
  height: 20px;
  background: white;
  color: $hightlight-color;
  border: 1px solid $hightlight-color;
  border-radius: 50%;
  line-height: 100%;
  cursor: pointer;
}
