@import "../variables.scss";

.header {
  background-color: $block-background-color;
  grid-area: header;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0px 10px;
  font-weight: 700;
  & img {
    margin-left: 5px;
    width: 32px;
    height: 32px;
  }
}
.header-logo {
  display: flex;
  align-items: center;
}
.header-menu {
  display: flex;
  gap: 15px;
}

.header-login {
  cursor: pointer;
}
